import React, { useMemo } from "react";
import { H1, H2, H3 } from "../Header";
import Link from "../Link";
import CarouselWrapper from "../Carousel";

import {GatsbyImage, getImage} from "gatsby-plugin-image";
import PetitsAnimauxImg from "../../../images/petit_animal.jpg";

import "./styles.scss";

const PetitsAnimauxProductCard = (props) => {
  const {node} = props;
  return (
    <div className="PetitsAnimauxCardContainer">
      <div className="PetitsAnimauxCard">
        <div className="PetitsAnimauxCardImage">
          <GatsbyImage 
            image={getImage(node.coverPhoto.file)} 
            alt={node.name}
          />
        </div>
        <H3 className="PetitsAnimauxCardTitle">{node.name}</H3>

        { 
          node.dimensions.map((str, idx) => {
            return <p className="PetitsAnimauxDescription" key={idx}>{str}</p>
          })
        }

        <H3 className="PetitsAnimauxCardPrice">USD ${node.price}</H3>
        <Link key={node.id} className="PetitsAnimauxCardLink" href={node.stripeUrl} useTheming={false}>Buy</Link>
      </div>
    </div>
  );
}

const PetitsAnimauxSection = (props) => {
  const {productMetadata} = props;
  const { metadata, products } = productMetadata;

  if (products.length === 0) {
    return null;
  }

  return (
    <div key={productMetadata.type}>
      <H2>{metadata.title}</H2>

    <div style={{height: '500px'}}>
      <CarouselWrapper>
        {products.map((node) => {
          return (
            <PetitsAnimauxProductCard key={node.id} node={node} />
          );
        })}
      </CarouselWrapper>
    </div>
    </div>
  );
}

const PetitsAnimaux = (props) => {
  const { productTypeMapping, products, photos } = props;
  const normalizedProducts = useMemo(
    () => {
      const categories = {};

      products.forEach(product => {
        const { category } = product;
        if (productTypeMapping[category]) {
          if (!categories[category]) {
            categories[category] = [];
          }

          categories[category].push({
            ...product,
            coverPhoto: photos.find(photo => (photo.description || "").trim() === product.coverPhotoId.trim()),
            dimensions: [
              product.length ? `Length: ${product.length}` : null,
              product.width ? `Width: ${product.width}` : null,
              product.height ? `Height: ${product.height}` : null,
            ].filter(a => a),
          });
        }
      });

      const res = Object.keys(categories)
        .filter(key => categories[key].length > 0)
        .map(key => {

          return {
            type: key,
            products: categories[key],
            metadata: productTypeMapping[key],
          };
        });

      return res;
    },
    [productTypeMapping, products, photos]
  );

  return (
    <div className="PetitsAnimauxSection">
      <img style={{ width: "100%" }} src={PetitsAnimauxImg} alt="Petits Animaux Banner" />

      <H1>Products</H1>

      {normalizedProducts.map((productMetadata) => {
        return (
          <PetitsAnimauxSection 
            key={productMetadata.type} 
            productMetadata={productMetadata} 
          />
        );
      })}
    </div>
  )
};

export default PetitsAnimaux;